import { Component, h, Prop } from '@stencil/core';

@Component({
	tag: 'ks-section'
})
export class Section {

    @Prop() muted: boolean = false;
    @Prop() dark: boolean = false;
    @Prop() darker: boolean = false;
    @Prop() expand: boolean = false;

    @Prop() marginTop: boolean = false;
    @Prop() mobileCollapse: boolean = false;

	render() {
        const sectionStyle = this.muted ?
            "uk-section-muted" :
            this.dark ? "uk-section-secondary" : "";

        const containerSize = this.expand ? "uk-container-expand" : "";

        const margin =
            "uk-margin-bottom " +
            (this.mobileCollapse ?
                "uk-padding-remove uk-padding-small@s uk-padding-remove-vertical@s uk-margin-remove-top " :
                "uk-padding-small uk-padding-remove-vertical ") + 
            (this.marginTop ? this.mobileCollapse ? "uk-margin-top@s " : "uk-margin-top " : "");

        const darker = {backgroundColor: "rgb(24, 24, 24)"};

		return(
			<div class={"uk-section uk-padding-remove-vertical " + sectionStyle} style={this.darker ? darker : null}>
                <div class={"uk-container " + containerSize + " " + margin}>
                    <slot/>
                </div>
            </div>
		);
	}
}
